:root {
  --font-body: "Josefin Sans", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --brightblue: hsl(220, 98%, 61%);
  --checkbackground: linear-gradient hsl(192, 100%, 67%) to hsl(280, 87%, 65%);
  --bgcolor: hsl(235, 21%, 11%);
  --cardbg: hsl(235, 24%, 19%);
  --itemtext: hsl(234, 39%, 85%);
  --texthover: hsl(236, 33%, 92%);
  --footertext: hsl(234, 11%, 52%);
  --verydarkgrayishblue100: hsl(233, 14%, 35%);
  --verydarkgrayishblue200: hsl(237, 14%, 26%);
}

.light-mode {
  --bgcolor: hsl(236, 33%, 92%);
  --cardbg: hsl(0, 0%, 98%);
  --itemtext: hsl(235, 19%, 35%);
  --texthover: hsl(235, 19%, 35%);
  --footertext: hsl(236, 9%, 61%);
  --lightgrayishblue: hsl(233, 11%, 84%);
  --verydarkgrayishblue100: hsl(235, 19%, 35%);
  background-image: url("../app/media/images/bg-mobile-light.jpg");
}
@media (min-width: 600px) {
  .light-mode {
    background-image: url("../app/media/images/bg-desktop-light.jpg");
  }
}

html {
  font-size: 100%;
  box-sizing: border-box;
}
html:focus-within {
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
  padding: 0;
}

body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
  font-family: var(--font-body);
}

img,
picture {
  max-width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
}

@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

ul,
ol {
  list-style: none;
}

.container {
  min-height: 100vh;
  max-width: 90rem;
  margin: 0 auto;
}

body {
  margin: 0 1rem;
  background-color: var(--bgcolor);
  background-image: url("../app/media/images/bg-mobile-dark.jpg");
  background-repeat: no-repeat;
  background-size: contain;
}
@media (min-width: 600px) {
  body {
    margin: 0;
    background-color: var(--bgcolor);
    background-image: url("../app/media/images/bg-desktop-dark.jpg");
    background-repeat: no-repeat;
    background-size: contain;
  }
}

nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 540px;
  margin: 4rem auto 2rem auto;
}
nav h1 {
  color: #fff;
  letter-spacing: 0.9rem;
  font-size: 1.5rem;
}
@media (min-width: 600px) {
  nav h1 {
    font-size: 2.5rem;
  }
}
nav .theme-toggle {
  background-color: transparent;
  border: none;
  scale: 0.75;
}
@media (min-width: 600px) {
  nav .theme-toggle {
    scale: 1;
  }
}

.card-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  max-width: 540px;
  margin: 0 auto;
  margin-top: 1rem;
  background-color: var(--cardbg);
  border-radius: 5px;
}
.card-container-footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 50px;
  padding: 0 1rem;
  color: var(--footertext);
  border-radius: 0 0 5px 5px;
  position: relative;
}
@media (min-width: 600px) {
  .card-container-footer {
    position: unset;
  }
}
.card-container-footer-items-left {
  font-size: 0.8rem;
}
.card-container-footer-categories {
  display: flex;
  justify-content: center;
  background-color: var(--cardbg);
  position: absolute;
  top: 115%;
  left: 50%;
  transform: translateX(-50%);
  padding: 1rem 5rem;
  border-radius: 5px;
  width: 100%;
}
@media (min-width: 600px) {
  .card-container-footer-categories {
    padding: 0;
    width: auto;
    position: relative;
    transform: unset;
    left: 0;
    top: 0;
  }
}
.card-container-footer-categories-all {
  padding: 0 1rem;
}
.card-container-footer-categories-all:hover {
  color: var(--texthover);
  cursor: pointer;
}
.card-container-footer-categories-active {
  padding: 0 1rem;
}
.card-container-footer-categories-active:hover {
  color: var(--texthover);
  cursor: pointer;
}
.card-container-footer-categories-completed {
  padding: 0 1rem;
}
.card-container-footer-categories-completed:hover {
  color: var(--texthover);
  cursor: pointer;
}
.card-container-footer-actions:hover {
  color: var(--texthover);
  cursor: pointer;
}
.card-drag-info {
  text-align: center;
  margin-top: 6rem;
  color: var(--footertext);
}

.current-tab {
  color: var(--brightblue);
}

footer {
  font-size: 11px;
  text-align: center;
}

footer a {
  color: #3e52a3;
}

.task-list {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 0;
}
.task-list-item {
  display: flex;
  align-items: center;
  position: relative;
  height: 4rem;
  padding-left: 3.5rem;
  border-bottom: 1px solid var(--footertext);
}
.task-list-item:hover {
  cursor: pointer;
}
.task-list-item:hover .task-list-remove {
  display: inline-block;
  cursor: pointer;
}
.task-list-text {
  margin-left: 1rem;
  color: var(--itemtext);
}
.task-list .completed {
  text-decoration: line-through;
  color: var(--footertext);
}
.task-list-check, .task-list-check-input {
  position: absolute;
  top: 45%;
  left: 20px;
  transform: translateY(-50%);
  background-color: var(--cardbg);
  width: 1.5em;
  height: 1.5em;
  border-radius: 50%;
  border: 1px solid var(--footertext);
  appearance: none;
  -webkit-appearance: none;
  outline: none;
  cursor: pointer;
}
.task-list-check:checked, .task-list-check-input:checked {
  background-image: url(../app/media/images/icon-check.svg), linear-gradient(#57ddff, #c058f3);
  background-repeat: no-repeat;
  background-position: center;
  border: none;
}
.task-list-check:hover, .task-list-check-input:hover {
  border: 1px solid var(--brightblue);
}
.task-list-remove {
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  width: 1.2rem;
  height: 1.2rem;
  margin-right: 1.5rem;
  border: none;
  background-image: url(../app/media/images/icon-cross.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 1.2rem;
  background-color: transparent;
}
@media (min-width: 600px) {
  .task-list-remove {
    display: none;
  }
}

.new-task {
  display: flex;
  justify-content: center;
  margin: 0 auto;
  max-width: 540px;
  position: relative;
  height: 4rem;
}
.new-task-input {
  width: 100%;
  padding-left: 4rem;
  background-color: var(--cardbg);
  color: var(--texthover);
  border-radius: 5px;
  border: none;
}

.theme-toggle:hover {
  cursor: pointer;
}